.radar {
  position: relative;
  height: 270px;
}
.radar .dian {
  width: 0px;
  height: 0px;
  background: #fff;
  border-radius: 50px;
  position: absolute;
  top: 50px;
  left: 50px;
  box-shadow: 0 0 5px 3px #fff, 0 0 10px 8px red;
}
.radar img {
  width: 258px;
  height: 169px;
  position: absolute;
  left: 70px;
  top: 40px;
}
.radar span {
  position: absolute;
  top: 38%;
  left: 44%;
  color: #fff;
  z-index: 99;
  font-size: 35px;
}
@keyframes move {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
