/* 攻击地图开始 */
#main{
  width: 100%;
  height: 80vh;
  z-index: 0;
  position: absolute;
  top: -30px;
}
.link:hover{
  transition: all .2s;
  transform: scale(1.05);
  cursor: pointer;
}
.left-bottom:hover{
  transform: scale(1);
}
/* 标题开始 */
    .title{
      position: absolute;
      top: 0;
      width: 100%;
      height: 80px;
      text-align: center;
      color: #f6f6f8;
      background: url(../assets/imgs/index/head.png) no-repeat;
      background-size: 100%;
      background-position: bottom center;
      z-index: 10;
      .txt-center{
            h1{
              font-size: 35px;
              font-weight: 400;
            }
            p{
              font-size: 14px;
              font-weight: 400;
            }
          }
    }
    /* 标题结束 */
/* 攻击地图结束 */
.hello{
  height: 100vh;
  position: relative;
  background: url(../assets/imgs/index/background.png) no-repeat;
  background-size: 100% 100%;


  .titles{
    color:#fff;
    font-size: 18px;
    font-weight: bold;
    text-indent: 30px;
    margin-bottom: 10px;
   }
  /* 今日传毒概况开始 */
  .left-top{
    width: 20%;
    height: 48%;
    position: absolute;
    top: 50px;
    left: 30px;
    .left-top-title{
      height: 45px;
      line-height: 45px;
      text-indent: 20px;
      color: #fff;
      font-size: 10%;
      background: url(../assets/imgs/index/daohang.png);
      background-size: 100% 100%;
    }
    .back{
      height: 100%;
      box-sizing: border-box;
      padding-top: 30px;
      background: url('../assets/imgs/index/zhanshi.png') no-repeat;
      background-size: 100% 100%;
      margin-top: 5px;
    }
    .linear-top{
      color: #fff;
      box-sizing: border-box;
      padding-left: 30px;
      /* 今日传毒概况标题开始 */
      .top-title{
        width: 80%;
        >div{
          display: inline-block;
        }
        >div:nth-child(1){
          margin-right: 30px;
        }
        p:nth-child(1) span{
          display: inline-block;
          width: 10px;
          height: 10px;
          border-radius: 50%;
          background: #76fcff;
        }

        p:nth-child(2){
          margin-top: 20px;
          span{
            font-size: 24px;
          }
        }

      }
      /* 今日传毒概况标题结束 */
      /* 今日传毒概况分割线开始 */
      .linear-title{
        position: relative;
        top: 20px;
        span{
          display: inline-block;
          width: 10px;
          height: 10px;
          border-radius: 50%;
          background: #76fcff;
          margin-right: 5px;
        }
      }
      /* 今日传毒分割线结束 */
    }
  }
  /* 今日传毒概况结束 */


  /* 近一周趋势开始 */
  .left-bottom{
    position: absolute;
    bottom: 20px;
    left: 30px;
    .let-bottom-back{
      background: url(../assets/imgs/index/dibu.png);
      background-size: 100% 100%;
      box-sizing: border-box;
      padding: 10px;
      margin-top: 5px;
      box-sizing: border-box;
      padding-top: 20px;
    }
  }
  /* 近一周趋势结束 */


  /* 今日漏洞情况开始 */
  .right-top{
    position: absolute;
    top: 50px;
    right: 30px;
    width: 450px;
    height: 300px;
    .right-top-title{
      width: 160px;
      height: 45px;
      line-height: 45px;
      text-indent: 20px;
      color: #fff;
      font-size: 18px;
      background: url(../assets/imgs/index/daohang.png);
      background-size: 100% 100%;
    }
    .right-top-back{
      height: 300px;
      background: url(../assets/imgs/index/dibu.png);
      background-size: 100% 100%;
      box-sizing: border-box;
      padding: 10px;
      margin-top: 10px;
    }
  }
  /* 今日漏洞情况结束 */
  /* 机房设备情况开始 */
  .right-center{
    position: absolute;
    top: 57%;
    right: 30px;
    width: 450px;
    height: 300px;
    margin-top: -150px;
    .right-center-title{
      width: 160px;
      height: 45px;
      line-height: 45px;
      text-indent: 20px;
      color: #fff;
      font-size: 18px;
      background: url(../assets/imgs/index/daohang.png);
      background-size: 100% 100%;
    }
    .right-center-back{
      height: 300px;
      background: url(../assets/imgs/index/dibu.png);
      background-size: 100% 100%;
      box-sizing: border-box;
      padding: 10px;
      margin-top: 5px;
      color: #fff;
      .box>div{
        width: 50%;
        display: inline-block;
      }
      .back-left{
        img{
          display: block;
          width: 172px;
          height: 172px;
          margin: 30px auto;
          margin-bottom: 10px;
        }
        p{
          text-align: center;
          color: #7cdcf5;
          span{
            margin-left: 10px;
          }
        }
      }
      .back-right{
        position: relative;
        top: -40px;
        ul{

          li{
            height: 40px;
            line-height: 40px;
            text-indent: 20px;
             span:nth-child(1){
              display: inline-block;
              width: 10px;
              height: 10px;
              border-radius: 50%;
              margin-right: 5px;
            }
            span:nth-child(2){
              margin: 0 8px;
            }
          }
          li:nth-child(1) span:nth-child(1){
            background: #6f6fad;
          }
          li:nth-child(2) span:nth-child(1){
            background: #e396aa;
          }
          li:nth-child(3) span:nth-child(1){
            background: #00ffff;
          }
          li:nth-child(4) span:nth-child(1){
            background: #b0a8cd;
          }
        }
      }
    }
  }
  /* 机房设备情况结束 */

  /* 视频监控设备情况开始 */
  .right-bottom{
    position: absolute;
    bottom: 20px;
    right: 30px;
    width: 450px;
    .right-bottom-title{
      width: 190px;
      height: 45px;
      line-height: 45px;
      text-indent: 20px;
      color: #fff;
      font-size: 18px;
      background: url(../assets/imgs/index/daohang.png);
      background-size: 100% 100%;
    }
    .right-bottom-back{
      height: 200px;
      background: url(../assets/imgs/index/dibu.png);
      background-size: 100% 100%;
      box-sizing: border-box;
      padding: 10px;
      margin-top: 5px;
      ul{
        margin-left: 25px;
        li{
          display: inline-block;
          margin-right: 55px;
        }
        color: #fff;
        margin-top: 20px;
        .img{
          position: relative;
          display: inline-block;
          i{
            display: inline-block;
            position: absolute;
            top: 15px;
            left: 27px;
            width: 20px;
            height: 20px;
            font-size: 25px;
          }
        }
        p{
          text-align: center;
          margin: 10px 0;
        }
        p:nth-child(3){
          color: #e1f5fe;
          font-size: 14px;
        }
      }
    }
  }
  /* 视频监控设备情况结束 */

  /* 网络实施安全攻击事件开始 */
  .bottom{
    width: 880px;
    height: 250px;
    position: absolute;
    bottom: 20px;
    left: 50%;
    margin-left: -460px;
    color: #fff;
    .bottom-title{
      color: #b6f0f2;
      text-align: center;
      height: 40px;
      line-height: 40px;
      font-size: 20px;
      font-weight: 600;
    }
    .attack-wrap{
      height: 185px;
      overflow: hidden;
    }
    .bottom-back{
      background: url(../assets/imgs/index/wangge.png);
      box-sizing: border-box;
      padding: 13px 8px;
    }
    ul{
      li{
        &:hover{
          background: rgb(0,48,98);
          cursor: pointer;
        }
        height: 25px;
        line-height: 25px;
        font-size: 14px;
        color: #7296a4;
        box-sizing: border-box;
        padding-left: 40px;
        span{
          display: inline-block;
          width: 2px;
          height: 20px;
          background: #ff7a01;
          margin-right: 10px;
          margin-top: 2px;
          vertical-align: top;
        }
        p{
          display: inline-block;
          margin-left: 10px;
          color: #66bad6;
          vertical-align: top;
        }
      }
    }
  }
  /* 网络安全实施攻击事件结束 */
}

