<script>
  import '../assets/js/china.js'//中国地图数据
  import map1 from '../components/index/map.vue'//攻击地图组件
  import editor from '../components/index/editor.vue'//近一周趋势组件
  import radar from '../components/index/radar.vue'//今日漏洞组件
  import linear from '../components/index/linear.vue'//今日传毒概况组件

  import countTo from 'vue-count-to';
  import BScroll from 'better-scroll';

  import {getData,getMachineSurvey,getMonitoring,getAttackTop20} from '../api/index/situation.js';
export default {
  data(){
    return{
      virus: {},
      machine: {},
      surveillance: {},
      attack: [],
      timer: 1,
      h: 0,
      scroll3: {},
      sumh: 0
    }
  },
  methods: {
    play(){
      let sumh = -this.attack.length*25;
      this.sumh = sumh;
      if(-sumh < 185){
        return;
      }
      this.timer = setInterval(() => {
        this.h--;
        if(this.h<= sumh){
          this.h = 0;
        }
        this.scroll3.scrollTo(0,this.h);
      },1000/20);
    },
    clear(){
      clearInterval(this.timer)
    }
  },
  mounted(){
    getAttackTop20().then(res => {//网络安全实施攻击事件
      console.log('网络安全实施攻击事件',res);
      this.attack = res.result;
      setTimeout(() => {
        this.scroll3 = new BScroll(this.$refs.content)
        this.play()

      },500);
    })
    getMonitoring().then(res => {//视频监控设备情况
      //console.log('视频监控设备情况',res);
      this.surveillance = res.result;
    })
    getMachineSurvey().then(res => {//机房设施情况
      //console.log('机房设施情况',res);
      this.machine = res.result;
    })
    getData().then(res => {//病毒类型分布
      this.virus = res.result;
    })
    setInterval(() => {
      getData().then(res => {//病毒类型分布
        this.virus = res.result;
      })
    },10000);
  },
  components: {
    map1,
    editor,
    radar,
    linear,
    countTo
  },
}
</script>

